.textError{
    font-weight: 700;
    line-height: 1.35;
    text-align: center;

    width: 280px;
    height: 66px;

    margin-top: 80px;
    margin: auto;
    font-size: 24px;

    animation: pulsate 2s infinite;
}

@keyframes pulsate {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1); 
    }
    100% {
      transform: scale(1);
    }
  }


@media screen and (min-width: 768px) {
    .textError{
        width: 450px;
        height: 42px;

        font-size: 32px;
        font-weight: 600;
        margin-top: 60px;
        margin-left: 159px;
    }
}

@media screen and (min-width: 1280px) {
    .textError{
        width: 450px;
        height: 42px;

        font-size: 32px;
        font-weight: 600;
        margin-top: 60px;
        margin-left: 415px;
    }
}

.span {
    display: block;
    @media screen and (min-width: 767px) {
        display: inline-block;
    }
}
.NotFoundImg{
    margin-top: 80px;
    margin: auto;
    /* padding-left: 20px;
  padding-right: 20px; */
}

.PageNotFoundBtn{
        display: flex;
        width: 248px;
        height: 40px;
        justify-content: center;
        align-items: center;

        padding: 8px 28px;
        border-radius: 40px;
        gap: 12px;
        border: 2px solid #54ADFF;
        background: #54ADFF;

        font-family: Manrope;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.64px;
        color: #fef9f9;
    
        margin-top: 248px;
        margin: auto;
        cursor: pointer;
        

    }

    @media screen and (min-width: 768px) {
        .PageNotFoundBtn {
            margin-top: 70px;
            /* margin-left: 260px; */
        }
    }
    
    @media screen and (min-width: 1280px) {
        .PageNotFoundBtn {
            margin-top: 18px;
            /* margin-left: 516px; */
        }
    }
.PageNotFoundBtn:hover,
.PageNotFoundBtn:focus {
    background: var(--gradient-blue);
}

